@import '../vars';

.stripes {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    display: flex;

    .s1, .s2, .s3 {
        box-shadow: 0 0 0.4em $cd-blue-1000;
    
        transition: flex 0.1s;
        flex: 0 0 1.5em;
    
        &:hover {
            flex: 0 0 2em;
        }
    }
    
    .s1 {
        background-color: $cd-blue-400;
        z-index: 1;
    }
    
    .s2 {
        background-color: $cd-blue-600;
        z-index: 2;
    }
    
    .s3 {
        background-color: $cd-blue-800;
        z-index: 3;
    }
}

.header-container {
    position: relative;

    overflow: hidden;
    max-width: 100%;
    width: 100%;

    box-shadow: 0 0 0.4em change-color($cd-blue-1000, $alpha: 0.5);
 
    //z-index: 1;
}

.header {
    margin-left: 6em;
    margin-right: 1em;
    display: grid;

    p a {
        margin-left: 0.5em;
    }

    @media only screen and (max-width: 600px) {
        grid-template:
            "name"
            "email"
            "twitter"
            "github"
            / auto;

        
        p {
            margin-top: 0;
        }
    }   

    @media only screen and (min-width: 600px) {
        grid-template:
            "name email"
            "name twitter"
            "name github"
            / auto auto;
            
        p {
            margin-top: default;
        }
    }

    
    @media only screen and (min-width: 800px) {
        grid-template:
            "name email twitter github"
            / auto auto auto;
            
        p {
            margin-top: default;
        }
    }

    * {
        justify-self: left;
        align-self: center;
    }

    h1 a {
        text-decoration: none;
        
        color: $cd-black;
        
        :link {
            color: $cd-black;
        }
        :visited {
            color: $cd-black;
        }

        @media (prefers-color-scheme: dark) {
            color: $cd-white;
    
            :link {
                color: $cd-white;
            }
            :visited {
                color: $cd-white;
            }   
        }
    }

    flex: 1 0 auto;
    max-width: 100%;
}