.contentcontainer {
    margin-top: 2em;
    display: flex;
    align-items: center;
    justify-content: center;
}

.content {
    position: relative;
    flex: 1 1 auto;

    display: flex;
    flex-direction: column;
    align-items: center;

    &>p {

        width: 90%;
        max-width: 800px;
        &:last-of-type {
            margin-bottom: 3em;
        }
    }
    
    
}

