@import '../vars';
$title-offset: 50%;
$hover-offset: 1em;

.article-title {

    width: 100%;
    height: 10em;
    margin-bottom: 1em;

    position: relative;

    &:hover .title-text {
        left: calc(#{$title-offset} + #{$hover-offset});
    }

    &:hover .title-image {
        left: -$hover-offset;
        .title-text {
            left: calc(#{$title-offset} + #{$hover-offset * 2})
        }
    }


    .title-image {
        position: absolute;
        top: 0;
        left: 0;

        transition: left 0.2s;

        z-index: 1;

        width: 100%;
        height: 10em;

        mask-image: linear-gradient(120deg, rgba(255,255,255,1), rgba(255,255,255,1) 49.85%, rgba(255,255,255,0) 50%);
        background-color: $cd-grey;

        user-select: none;
        overflow: hidden;
        
        img {
            width: 100%;
        }

        .title-text {
            color: $cd-blue-300;
            mix-blend-mode: color-dodge;
        }
    }

    .title-text {
        position: absolute;
        transform: translateX(-50%) translateY(-50%);
        left: $title-offset;
        top: 5em;
        width: 90%;
        text-align: center;

        transition: left 0.2s;
        h1 {
            font-size: 3.5em;
        }

    }
    
}